.info {
    margin-left: 100px;
    margin-right: 100px;
    display: flex;
    justify-content: space-between;

    &__left {
        display: flex;
        flex-direction: column;
        align-self: flex-start;
        color: $color-1;
        width: 70%;

        &--title {
            font-size: 36px;
            font-weight: 500;
            overflow: visible;
            white-space: nowrap;
            margin-bottom: inherit;
        }

        &--town {
            font-size: 18px;
            margin-top: inherit;
            font-weight: 500;
        }

        &--ref {
            width: 7.188rem;
            height: 25px;
            margin-bottom: 10px;
        }
    }

    &__right {
        display: flex;
        flex-direction: column;
        color: $color-1;
        width: 50%;
        align-self: flex-end;

        &--detail {
            display: flex;
            flex-direction: row;
            justify-content: flex-end;
            align-items: center;
            margin-top: 25px;
        }

        &--name {
            width: 5.813rem;
            font-size: 18px;
            font-weight: 500;
            margin-top: 15px;
        }

        &--picture {
            border-radius: 50%;
            width: 4rem;
            height: 4rem;
            margin-left: -8px;
        }

        &--stars {
            margin-bottom: 3.125rem;
        }
    }
}