.about__page {
    width: calc(100% - 400px);
    margin: 5px auto 7px auto;

    @media (max-width: 539.99px) {
        width: 93%;
        margin: 20px auto 7px auto;
    }
    @media screen and (min-width: 767.99px) and (max-width: 1023.99px) {
        width: calc(100% - 300px);
    }
    .dropdown__header {
        height: 47px;
        margin-bottom: 15px;
        @media screen and (max-width: 424.99px) {
            height: 30px;
            width: 100%;
            margin-bottom: inherit;
        h4 {
            font-size: 13px;
        }
   
        }
        @media screen and (min-width: 1024.99px) {
            h4 {
                font-size: 24px;
            }
            margin-bottom: inherit;
        }
    
    .dropdown__content {
        &--li {
            margin-top:-16px ;
        } 
    }
}
}